.image-gallery {
  width: 75%; }

.image-gallery-swipe {
  border: solid 1px #dcdcdf; }

.image-gallery-image {
  text-align: center;
  height: 350px;
  display: -ms-flexbox;
  display: flex; }
  .image-gallery-image img {
    -ms-flex-item-align: center;
        align-self: center;
    width: 100%;
    height: auto; }

.image-gallery-thumbnails-wrapper {
  margin-top: 30px; }

.image-gallery-thumbnail + .image-gallery-thumbnail {
  margin-left: 5px; }

.image-gallery-thumbnail {
  border: solid 1px #dcdcdf;
  width: auto; }
  .image-gallery-thumbnail.active {
    border: solid 1px #ff4572; }
  .image-gallery-thumbnail img {
    height: 40px;
    width: auto;
    max-width: 100px; }



