body {
  margin: 0;
  padding: 0; }

* {
  font-family: 'Nunito', sans-serif; }

.HeaderBackground {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#ff4572), to(#2a079b));
  background-image: -webkit-linear-gradient(bottom, #ff4572, #2a079b);
  background-image: -o-linear-gradient(bottom, #ff4572, #2a079b);
  background-image: linear-gradient(to top, #ff4572, #2a079b); }



